<template>
  <div class="container-news1" :style="{opacity:isImgLoading}">
    <div class="container-news-down">
      <img src="@/assets/news/news1/1.png" alt="" class="container-news-down-img">
    </div>
    <div class="container-news-content">
      <div class="container-news-content-title">
        <Title title1="焦点" title2="新闻" background1="white" background2="white" :isWhite="true" :isTextWhite="true"></Title>
      </div>
      <div class="container-news-content-hidden">
        <div class="container-news-content-video">
        <ul class="container-news-content-video1" ref="video1Wrapper" :style="{'transition-duration':`${speel}ms`}">
          <li class="container-news-content-video1-content" v-for="(ele,n) in some" :key="n" ref="videoNews" @mousedown="changeMove($event,n)" @touchstart="changeYD($event,n)">
            <Card :isBG="true" isShow="flex" :title1="ele.text1" :title2="ele.text2" :title3="ele.text3" isBtn='block' :src="ele.src"></Card>
          </li>
        </ul>
      </div>
      </div>
      <ul class="container-news-content-dian">
        <li class="container-news-content-dian1" v-for="(ele,n) in some" :key="n" :class="{'container-news-content-dian1-active':n == MathActive}"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import Title from '@/components/Title'
import Card from '@/components/Card'
import Urlimg1 from '@/assets/news/news1/3.jpg'
import Urlimg2 from '@/assets/news/news1/4.jpg'
import Urlimg3 from '@/assets/news/news1/5.jpg'
import Urlimg4 from '@/assets/news/news1/6.jpg'
import forImage from "@/mixins/forImage.js"
export default {
  mixins:[forImage([require('@/assets/news/news1/1.png')])],
components:{
  Title,
  Card
},
data() {
  return {
    some:[
      {
        src:`${Urlimg1}`,
        text1:"新闻 公告",
        text2:"【英雄联盟手游】2.5版本开发者日志",
        text3:"英雄联盟手游2.5版本开发者日志"
      },
      {
        src:`${Urlimg2}`,
        text1:"新闻 公告",
        text2:"【英雄联盟手游】2.5版本开发者日志",
        text3:"英雄联盟手游2.5版本开发者日志"
      },
      {
        src:`${Urlimg3}`,
        text1:"新闻 公告",
        text2:"【英雄联盟手游】2.5版本开发者日志",
        text3:"英雄联盟手游2.5版本开发者日志"
      },
      {
        src:`${Urlimg4}`,
        text1:"新闻 公告",
        text2:"【英雄联盟手游】2.5版本开发者日志",
        text3:"英雄联盟手游2.5版本开发者日志"
      }
    ],
    rect:null,
    newL:0,
    speel:0,
    MathActive:0,
    PCwidth:0,
  }
},
mounted() {
   if(document.body.clientWidth <= 1024){
    this.PCwidth = this.$refs.videoNews[0].getBoundingClientRect().width + 37.5
   }else{
    this.PCwidth = this.$refs.videoNews[0].getBoundingClientRect().width + 54
   }
},
methods: {
  changeMove(e,n){
   let disX = e.clientX - this.$refs.video1Wrapper.offsetLeft;
  let that = this;
  let spaceL = 0;
  let p = n;
  let timer = null;
  let firstTime=0;
  let lastTime = 0;
  let spaceA = 0;
  let direction = 0;// eslint-disable-line no-unused-vars
   firstTime = new Date().getTime();
  if(n == this.some.length - 1){
      spaceL = this.newL;
  }
  spaceA = this.newL;
  // console.log(e)
  document.onmousemove = function(e){
    that.newL = e.clientX - disX;
    that.speel = 0;
    direction = that.newL - spaceA;
    if(that.newL >= 0){
      that.$refs.video1Wrapper.style.left = that.newL/3 +'px';
    }else if(that.newL <= -that.PCwidth*(that.some.length-1) && p == that.some.length - 1){
      let jL = that.newL - spaceL;
      that.$refs.video1Wrapper.style.left = spaceL + jL/2 + 'px';
    }
    else{
      that.$refs.video1Wrapper.style.left = that.newL + 'px';
    }
  };
  // console.log(e.preventDefault)
  document.onmouseup = function(){
    document.onmouseup = null;
    document.onmousemove = null;
    lastTime = new Date().getTime();
    if(lastTime - firstTime <= 600 ){
      that.newL = spaceA;
      if(direction < 0 ){
        that.$refs.video1Wrapper.style.left = that.newL - that.PCwidth + 'px';
        that.newL = that.newL - that.PCwidth;
      }else if(direction > 0){
        that.$refs.video1Wrapper.style.left = that.newL + that.PCwidth + 'px';
        that.newL = that.newL + that.PCwidth;
      }else{
        that.$refs.video1Wrapper.style.left = that.newL + 'px'
      }
      
    } 
    else{
      that.newL = spaceA;
      if(direction > 0){
        that.$refs.video1Wrapper.style.left = Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth + that.newL +'px';
        that.newL =that.newL + Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth;
      }else{
         that.$refs.video1Wrapper.style.left =that.newL - Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth +'px';
        that.newL = that.newL - Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth;
      }
      }
    that.speel = 300;
    if(that.newL >= 0){
      that.newL = 0;
      that.$refs.video1Wrapper.style.left = 0  +'px';
    }else if(that.newL <= -that.PCwidth*(that.some.length-1)){
      that.newL = -that.PCwidth*(that.some.length-1);
      that.$refs.video1Wrapper.style.left = -that.PCwidth*(that.some.length-1) + 'px'
    }
    that.MathActive = Math.abs(that.newL/that.PCwidth);
    clearTimeout(timer);
    setTimeout(()=>{
      that.speel = 0;
    },300)
  }
  },
  changeYD(e,n){
  //  this.rect=this.$refs.video1Wrapper.getBoundingClientRect();
   let disX = e.touches[0].pageX - this.$refs.video1Wrapper.offsetLeft;
  //  console.log(this.rect.left)
  let that = this;
  let spaceL = 0;
  let p = n;
  let timer = null;
  let firstTime=0;
  let lastTime = 0;
  let spaceA = 0;
  let direction = 0;// eslint-disable-line no-unused-vars
   firstTime = new Date().getTime();
  if(n == this.some.length - 1){
      spaceL = this.newL;
  }
  spaceA = this.newL;
  // console.log(e.touches[0].pageX)
  // console.log(e.pageX)
  document.ontouchmove = function(e){
    that.newL = e.touches[0].pageX - disX;
    that.speel = 0;
    direction = that.newL - spaceA;
    if(that.newL >= 0){
      that.$refs.video1Wrapper.style.left = that.newL/3 +'px';
    }else if(that.newL <= -that.PCwidth*(that.some.length-1) && p == that.some.length - 1){
      let jL = that.newL - spaceL;
      that.$refs.video1Wrapper.style.left = spaceL + jL/2 + 'px';
    }
    else{
      that.$refs.video1Wrapper.style.left = that.newL + 'px';
    }
  };
  // console.log(e)
  document.ontouchend = function(){
    document.ontouchend = null;
    document.ontouchmove = null;
    lastTime = new Date().getTime();
    if(lastTime - firstTime <= 600 ){
      that.newL = spaceA;
      if(direction < 0 ){
        that.$refs.video1Wrapper.style.left = that.newL - that.PCwidth + 'px';
        that.newL = that.newL - that.PCwidth;
      }else if(direction > 0){
        that.$refs.video1Wrapper.style.left = that.newL + that.PCwidth + 'px';
        that.newL = that.newL + that.PCwidth;
      }else{
        that.$refs.video1Wrapper.style.left = that.newL + 'px'
      }
      
    } 
    else{
      that.newL = spaceA;
      if(direction > 0){
        that.$refs.video1Wrapper.style.left = Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth + that.newL +'px';
        that.newL =that.newL + Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth;
      }else{
         that.$refs.video1Wrapper.style.left =that.newL - Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth +'px';
        that.newL = that.newL - Math.round(Math.abs(direction)/that.PCwidth)*that.PCwidth;
      }
      }
    that.speel = 300;
    if(that.newL >= 0){
      that.newL = 0;
      that.$refs.video1Wrapper.style.left = 0  +'px';
    }else if(that.newL <= -that.PCwidth*(that.some.length-1)){
      that.newL = -that.PCwidth*(that.some.length-1);
      that.$refs.video1Wrapper.style.left = -that.PCwidth*(that.some.length-1) + 'px'
    }
    that.MathActive = Math.abs(that.newL/that.PCwidth);
    clearTimeout(timer);
    setTimeout(()=>{
      that.speel = 0;
    },300)
  }
  },
},
}
</script>

<style scoped lang="less">
@media(min-device-width:1025px){
  .container-news1{
    width: 100%;
    height: 9.6rem;
    padding-top: 1.5rem;
    padding-bottom: 7.4rem;
    background: linear-gradient(45deg, #2858f0, #32c8ff);
    overflow: hidden;
    position: relative;
    .container-news-down{
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 0;
      .container-news-down-img{
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 0;
        transform: translateY(30%);
      }
    }
    .container-news-content{
      width: 23.7rem;
      height: 100%;
      margin: 0 auto;
      position: relative;
      // background: yellowgreen;
      .container-news-content-title{
        width: 15rem;
        height: 3rem;
      }
      .container-news-content-hidden{
        height: 6.4rem;
        width: 36.7rem;
        position: absolute;
        left: 5rem;
        top: 2rem;
        overflow: hidden;
        .container-news-content-video{
        width: 11.5rem;
        height: 100%;
        position: relative;
        // border: 0.05rem solid black;
        // background: yellowgreen;
        overflow: visible;
        // clip-path: polygon(0 0, 100% 0, 100% calc(100% - 0.8rem), calc(100% - 0.8rem) 100%, 0 100%);
        .container-news-content-video1{
          height: 100%;
          // width: 47.5rem;
          display: flex;
          flex-direction: row;
          position: absolute;
          left: 0;
          top: 0;
          transition: all 0ms;
          li{
            height: 100%;
            width: 11.5rem;
            clip-path: polygon(0 0, 100% 0, 100% calc(100% - 0.8rem), calc(100% - 0.8rem) 100%, 0 100%);
            margin-right: 1rem;
          }
          :last-child{
            margin-right: 0rem;
          }
        }
      }
      }
      .container-news-content-dian{
        height: 0.5rem;
        width: 15rem;
        position: absolute;
        left: 0;
        bottom: 1rem;
        display: flex;
        flex-direction: row;
        .container-news-content-dian1{
          width: 0.4rem;
          height: 100%;
          position: relative;
          &::after{
            content: "";
            width: 0.15rem;
            height: 0.15rem;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%) rotate(45deg);
            background: rgba(255, 255, 255, 0.4);
          }
        }
        .container-news-content-dian1-active{
          &::after{
            background: white;
          }
        }
      }
    }
  }
}







