  .container-news1{
    width: 100%;
    height: 12rem;
    padding-top: 1rem;
    padding-bottom: 3.4rem;
    background: linear-gradient(45deg, #2858f0, #32c8ff);
    overflow: hidden;
    position: relative;
    .container-news-down{
      width: 100%;
      position: absolute;
      left: 0;
