        bottom: 0;
        transform: translateY(30%);
      }
    }
    .container-news-content{
      width: 100%;
      height: 100%;
      margin: 0 auto;
      position: relative;
      // background: yellowgreen;
      .container-news-content-title{
        width: 100%;
      }
      .container-news-content-hidden{
        height: 7rem;
        width: 8.6rem;
        position: absolute;
        left: 0.7rem;
        top: 4rem;
        overflow: hidden;
        .container-news-content-video{
        width: 100%;
        height: 100%;
        position: relative;
        // border: 0.05rem solid black;
        // background: yellowgreen;
        overflow: visible;
        // clip-path: polygon(0 0, 100% 0, 100% calc(100% - 0.8rem), calc(100% - 0.8rem) 100%, 0 100%);
        .container-news-content-video1{
          height: 100%;
          // width: 47.5rem;
          display: flex;
          flex-direction: row;
          position: absolute;
          left: 0;
          top: 0;
          transition: all 0ms;
          li{
            height: 100%;
            width: 8.6rem;
            clip-path: polygon(0 0, 100% 0, 100% calc(100% - 0.8rem), calc(100% - 0.8rem) 100%, 0 100%);
            margin-right: 0.5rem;
          }
          :last-child{
            margin-right: 0rem;
          }
        }
      }
      }
      .container-news-content-dian{
        height: 0.5rem;
        width: 100%;
        position: absolute;
        left: 0.7rem;
        bottom: 0;
        display: flex;
        flex-direction: row;
        .container-news-content-dian1{
          width: 0.4rem;
          height: 100%;
          position: relative;
          &::after{
            content: "";
            width: 0.15rem;
            height: 0.15rem;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%) rotate(45deg);
            background: rgba(255, 255, 255, 0.4);
          }
        }
        .container-news-content-dian1-active{
          &::after{
            background: white;
          }
        }
      }
    }
  }
}
</style>