<template>
  <ul class="container-navmin">
    <div class="container-navmin-black"></div>
    <div class="container-navmin-top"></div>
    <div class="container-navmin-bottom"></div>
    <div class="container-navmin-chired" :class="{'container-navmin-chired-active':isActive == 4}">
