export default function(imgages = []) {
    return {
        data() {
            return {
                isImgLoading: 0
            };
        },
        beforeCreate() {
            let imgs = imgages;
            for (let img of imgs) {
                let imgage = new Image();
                imgage.onload = () => {
                    this.isImgLoading = 1;
                };
                imgage.src = img
            }
        },
    }
}